package com.example.redwood.counter.compose

import androidx.compose.runtime.Composable
import app.cash.redwood.LayoutModifier
import app.cash.redwood.RedwoodCodegenApi
import app.cash.redwood.compose.RedwoodComposeNode
import com.example.redwood.counter.widget.SchemaWidgetFactoryProvider
import com.example.redwood.counter.widget.Text
import kotlin.OptIn
import kotlin.String
import kotlin.Unit

@Composable
@OptIn(RedwoodCodegenApi::class)
public fun Text(text: String?, layoutModifier: LayoutModifier = LayoutModifier): Unit {
  RedwoodComposeNode<SchemaWidgetFactoryProvider<*>, Text<*>>(
      factory = { it.Schema.Text() },
      update = {
        set(layoutModifier) { layoutModifiers = it }
        set(text, Text<*>::text)
      },
      content = {
      },
      )
}
